"use strict";

$(function () {
  //custom validations
  jQuery.validator.addMethod("alphanumber", function (value, element) {
    return this.optional(element) || /^[A-Za-z0-9\-\s]+$/i.test(value);
  }, "Letters and spaces only please");
  //-------------------------------------------------------------------------------------------------
  jQuery.validator.addMethod("address", function (value, element) {
    return this.optional(element) || /^[a-z0-9\s,#-]*$/i.test(value);
  }, "Letters and spaces only please");
  //-------------------------------------------------------------------------------------------------
  jQuery.validator.addMethod("city", function (value, element) {
    return this.optional(element) || /^[a-z0-9\s,.-]*$/i.test(value);
  }, "Letters and spaces only please");
  //-------------------------------------------------------------------------------------------------
  jQuery.validator.addMethod("password", function (value, element) {
    return this.optional(element) || /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/i.test(value);
  }, "Letters and spaces only please");
  //-------------------------------------------------------------------------------------------------
  jQuery.validator.addMethod("phone", function (value, element) {
    return this.optional(element) || /^[0-9 ]+$/i.test(value);
  }, "Letters and spaces only please");
  //-------------------------------------------------------------------------------------------------
  jQuery.validator.addMethod("zip", function (value, element) {
    return this.optional(element) || /(^\d{5}$)|(^\d{5}-\d{4}$)/.test(value);
  }, "Letters and spaces only please");

  if ($('.error-bar').length > 0) {
    $('.error-bar i').on('click', function () {
      $('.error-bar').removeClass('show');
    });
  } //if

  if ($("form#login").length > 0) {
    $("form#login").validate();
  }
  if ($("form#registration").length > 0) {

    $("input#email").on('change', function (e) {
      $(this).val().includes(".k12") || $(this).val().includes(".edu") ? $(this).siblings('.warning').show() : $(this).siblings('.warning').hide();
    });

    var $submit = $("#submit");
    registration = $("form#registration").validate();
  } //if

  //mask fields
  $("input#phone").mask("999 999 9999");
  $("input#zip").mask("99999");

  //show-password function
  if ($('#password').length > 0 && $('.showpass').length > 0) {
    $('.showpass').on('click', function () {
      var $x = $('#password');
      $x.prop("type") === "password" ? $x.prop("type", "text") : $x.prop("type", "password");
    });
  }

  $('input#password[type=password]').keyup(function () {
    var pswd = $(this).val();
    //length 8
    pswd.length < 8 ? $('#length').removeClass('ok').addClass('no') : $('#length').removeClass('no').addClass('ok');

    //validate letter
    pswd.match(/[a-zA-Z]/i) ? $('#letter').removeClass('no').addClass('ok') : $('#letter').removeClass('ok').addClass('no');

    //validate one-number
    pswd.match(/\d/i) ? $('#number').removeClass('no').addClass('ok') : $('#number').removeClass('ok').addClass('no');

    //validate no spaces
    pswd.match(/^\S*$/i) ? $('#spaces').removeClass('no').addClass('ok') : $('#spaces').removeClass('ok').addClass('no');

    //no special characters
    !pswd.match(/\`|\~|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\+|\=|\[|\{|\]|\}|\||\\|\'|\<|\,|\.|\>|\?|\/|\""|\;|\:|\s/g) ? $('#special').removeClass('no').addClass('ok') : $('#special').removeClass('ok').addClass('no');
  });

  $('input#password[type=password]').focus(function () {
    $(this).valid() ? $(this).removeClass('error') : $(this).addClass('error');
  });

  //remove messages on change
  if ($('select').length > 0) {
    $('select').customSelect({
      includeValue: true,
      transition: 400
    });
    $('select').on('change', function () {
      var valid = validate_select($(this).val());
      valid ? $(this).siblings('.select-error').hide() : $(this).siblings('.select-error').show();
    });
  }

  //if warm data exists
  if ($('.select-warm').length > 0) {
    $.each($('.select-warm'), function () {
      var warm = $(this).text().trim(),
          id = $(this).data("id"),
          select = $(this).siblings('.custom-select');
      //console.log(select);
      if (warm != "") {
        $("#" + id).val(warm);
        select.find('.custom-select__option--value').text(warm);
      }
    });
  }

  //functions
}); //function

function validateform() {
  registration.form();
  var dob = "",
      state = $("#state");
  $.each($('select.dob'), function () {
    var valid = validate_select($(this).val());
    if (valid) {
      dob += $(this).val();
    }
  });
  dob.length < 8 ? $(".dob-error").show() : $(".dob-error").hide();
  state.val().trim() != "" ? state.siblings('.select-error').hide() : state.siblings('.select-error').show();;

  if (registration.valid() && dob.length == 8) {
    return true;
  } else {
    return false;
  }
}
function validate_select(value) {
  var valid = "";
  return !value.trim() == "" ? valid = true : valid = false;
}