'use strict';

$(function () {
  //custom function showing current slide
  if ($('.slider-section').length > 0) {
    $.each($('.slider-section'), function () {
      var $status = $(this).find('.pagingInfoSlider');
      var $slickElement = $(this).find('.slider');

      $slickElement.on('init reInit afterChange', function (event, slick, currentSlide, nextSlide) {
        //currentSlide is undefined on init -- set it to 0 in this case (currentSlide is 0 based)
        var i = (currentSlide ? currentSlide : 0) + 1;
        $status.text(i + ' OF ' + slick.slideCount);
      });
    });
  }

  if ($('.slider').length > 0) {
    $(".product-card-carousel").slick({
      dots: true,
      infinite: false,
      slidesToShow: 3,
      slidesToScroll: 3,
      variableWidth: true,
      speed: 300,
      responsive: [{
        breakpoint: 1024,
        settings: {
          slidesToShow: 2
        }

      }, {

        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }

      }, {

        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          arrows: false,
          infinite: true
        }

      }]
    });
    $(".two-product-card-carousel").slick({
      dots: true,
      arrows: false,
      infinite: true,
      speed: 300,
      slidesToShow: 2,
      slidesToScroll: 2,
      variableWidth: true,
      // the magic
      responsive: [{
        breakpoint: 1024,
        settings: "unslick",
        variableWidth: true
      }, {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          dots: false
        }

      }, {

        breakpoint: 400,
        settings: {
          dots: false,
          arrows: false,
          infinite: true,
          slidesToShow: 1,
          slidesToScroll: 1
        }

      }]
    });
  }
  if (".c-014".length > 0) {
    $(".c-014 .slider").slick({
      dots: true,
      infinite: false,
      slidesToShow: 4,
      slidesToScroll: 4,
      speed: 300,
      variableWidth: true,
      responsive: [{
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1

        }

      }, {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
          variableWidth: true
        }

      }, {

        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          arrows: false,
          infinite: true,
          variableWidth: true
        }

      }]
    });
  } //c-014
  if (".c-006".length > 0) {
    $(".c-006 .slider").slick({
      dots: true,
      infinite: false,
      slidesToShow: 2,
      slidesToScroll: 2,
      speed: 300,
      variableWidth: true,
      responsive: [{
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1

        }

      }, {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
          variableWidth: true
        }

      }, {

        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          arrows: false,
          infinite: true,
          variableWidth: true
        }

      }]
    });
  } //c-014
});