"use strict";

$(document).ready(function () {
    $(".navbar-toggle").on("click", function () {
        $(this).toggleClass("collapsed");
        $("#mobile-profile").slideUp(250);
    });
    $('.dropdown-submenu > a').on("click", function (e) {
        $(this).next('ul').toggle();
        $(this).toggleClass('open');
        e.stopPropagation();
        e.preventDefault();
    });

    $('#login-top-mobile-button').on("click", function () {
        $("#mobile-profile").slideToggle(250);
        $('.nav-menu').toggleClass('box-shadow-bottom');
        $('.navbar-toggle').addClass('collapsed');
        $('.navbar-collapse').removeClass('in');
    });

    /*$('.mega-dropdown > .dropdown-toggle').on('click',function(e){
        e.preventDefault();
        e.stopPropagation();
        e.stopImmediatePropagation();
        $(this).parent().toggleClass('open-panel').siblings().removeClass('open-panel');
        var $navMenu= $('section.nav-menu');
        $navMenu.height('110');
        if($(this).parent().hasClass('open-panel')){
            var megaDropdownMenu = $(this).siblings('.mega-dropdown-menu').height();
            $navMenu.height($navMenu.height() + megaDropdownMenu + 50);
        }
    })*/
    if ($('#middle-menu').length > 0) {
        var distance = $('#middle-menu').offset().top;

        $(window).scroll(function () {
            if ($(this).scrollTop() >= distance) {
                $('#middle-menu').addClass('menu-fix');
            } else {
                $('#middle-menu').removeClass('menu-fix');
            }
        });
    }
});